<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title"></h5><button type="button" aria-label="Close" class="close" @click="$emit('close')"></button>
        </header>
        <div class="modal-body">
            <b-list-group flush>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.bet_id }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.player }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.type | toJpType }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.status | jpStatus }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.currency }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.win | formatMoney(rewardDetails.currency) }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.operator || '' }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.received_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.expired_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.updated_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span></span>{{ rewardDetails.created_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item>
                    <span></span>
                    <b-list-group-item v-for="(item, key) in rewardDetails.exchange_rate" :key="key" class="d-flex justify-content-between">
                        <span>{{ key }}</span>{{ item | formatMoney(key) }}
                    </b-list-group-item>
                </b-list-group-item>
            </b-list-group>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Close</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: ['rewardDetails']
}
</script>

<style>

</style>



