import Vue from 'vue'
import dayjs from 'dayjs';

const level = config.level;
const currency_point = config.currency_point;
const betTypes = config.betTypes;
const jpTypes  = config.jpTypes;
const notSetText = '';

Vue.filter('level', function (val) {
    if (val === null || val === undefined) return '';
    return level[val] ? level[val] : (':' + val);
})

Vue.filter('disabled', function (val) {
    switch (val) {
        case 0:
            return "";
        case 1:
            return "";
        default:
            return val;
    }
})

Vue.filter('mode', function (val) {
    switch (val) {
        case "fork_mode":
            return "";
        case "cluster_mode":
            return "";
        default:
            return val;
    }
})

Vue.filter('status', function (val) {
    switch (val) {
        case "online":
            return "";
        case "stopped":
            return "";
        case "errored":
            return "";
        default:
            return val;
    }
})

Vue.filter('bitsToMB', function (val) {
    return (val === null || val === undefined) 
        ? '' 
        : (val / 1024 / 1024).toFixed(2);
})

Vue.filter('toGidNameString', function (val) {
    if (!val || val.length === 0) return '';
    let gid_name = config.gid_name;
    let msg = '';
    for (let i = 0, len = val.length; i < len - 1; i++) {
        msg += `${gid_name[val[i]]} `;
    }
    msg += `${gid_name[val[val.length - 1]]}`;
    return msg;
})

Vue.filter('serverType', function (val) {
    switch(val) {
        case '1':
            return '';
        case '2':
            return '';
        default: 
            return notSetText + ':' + val;
    }
})

Vue.filter('betType', function (val) {
    return (!val || typeof betTypes[ val.toString() ] !== 'string') 
        ? notSetText + ':' + val 
        : betTypes[ val.toString() ];
})

Vue.filter('wsStatus', function (val) {
    switch(val) {
        case 0:
            return '';
        case 1:
            return '';
        default:
            return notSetText;
    }
})

Vue.filter('formatMoney', function (val, currency) {
    if (val === null || val === undefined) return '';
    let point = currency_point[currency] || 2; 
    return val.toFixed(point).replace(/(\d)(?=(\d{3})+\.)/g, '$1,');
})

Vue.filter('headCurrencyText', function (val, currency) {
    return (!currency || currency === 'NONE') ? val : `${val}(${currency})`;
})

Vue.filter('footCurrencyText', function (val, currency) {
    return (!currency || currency === 'NONE') ? val : `${currency}$ ${val}`;
})

Vue.filter('formatTime', function (val) {
    return val ? dayjs(val).format("YYYY-MM-DD HH:mm:ss") : '';
})

Vue.filter('toGidName', function (val) {
    if (!val) return ``;
    let gid_name = config.gid_name;
    if (!gid_name[val]) return `(${val})`
    return gid_name[val];
})

Vue.filter('toJpType', function (val) {
    return (!val || typeof jpTypes[ val.toString() ] !== 'string') 
        ? (notSetText + ':' + val) 
        : jpTypes[ val.toString() ];
})

Vue.filter('jpStatus', function (val) {
    switch(val) {
        case 0:
            return '';
        case 1:
            return '';
        case 2:
            return '';
        case 3:
            return '';
        default:
            return notSetText + ':' + val;
    }
})

Vue.filter('timeDuration', function (val, prefix = 1, has_sec = false) {
    if (!val) return '';
    val = Math.round(val / prefix);
    let day = Math.floor(val / (60 * 60 * 24));
    day = day >= 1 ? day : 0;
    let hour = Math.floor(val / (60 * 60));
    hour = hour >= 1 ? hour : 0;
    let min = Math.floor(val / (60));
    min = min >= 1 ? min : 0;
    let date = `${day}${Math.abs(hour - day * 24)}${Math.abs(min - hour * 60)}`;
    date += has_sec ? `${Math.abs(val - min * 60)}` : '';
    return date;
})

Vue.filter('timeDurationNoDay', function (val, prefix = 1, has_sec = false) {
    if (!val) return '';
    val = Math.round(val / prefix);
    let day = Math.floor(val / (60 * 60 * 24));
    day = day >= 1 ? day : 0;
    let hour = Math.floor(val / (60 * 60));
    hour = hour >= 1 ? hour : 0;
    let min = Math.floor(val / (60));
    min = min >= 1 ? min : 0;
    let date = `${Math.abs(hour - day * 24)}${Math.abs(min - hour * 60)}`;
    date += has_sec ? `${Math.abs(val - min * 60)}` : '';
    return date;
})

Vue.filter('gameListStatus', function (val) {
    switch(val) {
        case 0:
            return '';
        case 1:
            return '';
        default:
            return notSetText + ':' + val;
    }
})

Vue.filter('haveFun', function (val) {
    switch(val) {
        case 0:
            return '';
        case 1:
            return '';
        default:
            return notSetText + ':' + val;
    }
})

Vue.filter('groupSet', function (val) {
    switch(val) {
        case 0:
            return '';
        case 1:
            return '';
        case 2:
            return '';
        case 3:
            return '';
        default:
            return notSetText + ':' + val;
    }
})


